.contact {
  background: #e8e8e8;
  position: relative;
  img {
    position: absolute;
    width: 100%; }
  h2 {
    margin-bottom: 100px; }
  &__block {
    h3 {
      font-size: 18px;
      font-weight: 600; }
    span {
      font-size: 18px;
      margin-bottom: 20px;
      display: inline-block; }
    &__social {
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      &__block {
        margin-right: 40px;
        width: 80px;
        height: 80px;
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 80px;
          margin-right: 40px;
          height: 80px;
          border: 2px solid transparent;
          border-image: linear-gradient(to bottom, $greeny-blue 37%, #29413b);
          border-image-slice: 1;
          transition: all 0.3s;
          .icon {
            fill: $dark-grey-blue-two;
            width: 35px;
            height: 35px; }
          &:hover {
            background: $dark-grey-blue-two;
            .icon {
              fill: #ffffff;
              transition: all 0.3s; } } }
        p {
          margin-top: 15px;
          text-align: center;
          font-size: 16px;
          color: $dark-grey-blue-two; } } }
    form {
      h3 {
        margin-bottom: 20px; }
      input {
        background: transparent;
        display: block;
        border-bottom: 2px solid $warm-grey;
        width: 100%;
        box-sizing: border-box;
        padding: 22px;
        font-size: 16px;
        color: $denim;
        margin-bottom: 30px;
        &:focus {
          border-image: linear-gradient(to right, $greeny-blue 37%, #29413b);
          border-bottom: 2px solid transparent;
          border-image-slice: 1; } }
      input[type="submit"] {
        margin-top: 60px;
        background: #ffffff;
        font-size: 16.1px;
        font-weight: 500;
        letter-spacing: 1.6px;
        border: none;
        color: $dark-grey-blue-two;
        transition: all 0.3s;
        &:hover {
          background: $dark-grey-blue-two;
          color: #ffffff; } } } } }
